@import 'global/vars.scss';
@import 'global/mixin.scss';
@import 'global/global.scss';

.widget-area {
   
	flex-basis: 310px;
	flex-shrink: 0;

	a { 

		display: block;
		transition: .5s;
	}

	.items { text-align: left;
			
		&.back { 

			border:30px solid #eff8fd;
			margin-bottom: 30px;

			.items-content { background-color: $color-white;
		
				&:hover  a, 
				&:hover .meta-span { color: $color-green3; }
			} 
		}

		&.border {

		 	border-left: 1px solid #eff8fd;
			border-right: 1px solid #eff8fd;
			
			&:last-of-type { border-bottom: 1px solid #eff8fd; }

			&:hover { background-color: $color-green3;
				
				&:hover  a, 
				&:hover .meta-span { color: $color-white; }

			}
			
			 .items-content { 
				
				a { 

					text-align: left;
					font-size: 16px;
				}

				.meta { 

					justify-content: flex-start;
					padding: 5px 0;
					
					.messag { margin-left: 30px; }
				}
			}
		}

		.meta-span { color: $color-blue3; }

		.items-title-articl { padding: 5px 0; }
	}
}

.picture-aside {

	display: block;
	margin: 0 auto;
}

.short-navig { padding-top: 7px; }

.row-navig {

    display: block;
    background-color: #eef8fd;
    border-bottom: 1px solid $color-white;
    font-family: $font-sansbold;
    font-size: 18px;
    padding: 16px 20px;
    cursor: pointer;
    transition: .5s;

    &.opened:before { @include pseudo(inline-block, "\f0d8");}
		
    &:before{

    	@include pseudo(inline-block, "\f0d7");
    	font-family: $font-awesome;
    	padding-right: 20px;
    }
}

.row-navig:hover,
.row-navig.opened,
.hidd:hover {

	background-color: $color-green3;
    color: $color-white;
}

.total-number {

	font-family: $font-sans;
	float: right;
}

.hidden-block { display: none; 

	&.opened { display: block; }

	.hidd {

		background-color: transition; 
		font-family: $font-sans;
		padding: 16px 20px 16px 47px;
		border: 1px solid #eef8fd;
		border-top: 0;
	}
}

.asadvert-aside {

    width: 100%;
    height: 446px;
    padding: 30px 0;

    img { margin: 0 auto; }
}

.title-aside {

	display: block;
	background-image: url(/images/background/pattern_blue.png);
	background-repeat: repeat;
	@include font($font-sansbold, $color-white, 18px);
	padding: 16px 20px;

	&:before {

		@include pseudo(inline-block, "");
	
		width: 20px;
		border: 1px solid $color-white;
		margin-right: 20px;
		vertical-align: middle;
	} 
}